  fill: #d73981; }

/* Search */
header .below .search-field {
  float: right;
  position: relative; }

header .below .search-field input[type="search"] {
  border-radius: 30px;
  background: #f7f8fa;
  color: #FFFFFF;
  border: 2px solid #3e4a5a;
  font-size: 1rem;
  line-height: 2.85rem;
  padding: 0 40px 0 20px;
  width: 180px;
  background: #3e4a5a;
  line-height: 40px;
  height: 44px;
  box-sizing: border-box; }
  header .below .search-field input[type="search"]:focus {
    outline: none;
    border-color: #3583cc;
    box-shadow: 0px 0px 3px #3583cc; }
  header .below .search-field input[type="search"]::-webkit-input-placeholder {
    color: #FFF; }
  header .below .search-field input[type="search"]:-moz-placeholder {
    color: #FFF; }
  header .below .search-field input[type="search"]::-moz-placeholder {
    color: #FFF; }
  header .below .search-field input[type="search"]:-ms-input-placeholder {
    color: #FFF; }

header .below .search-field button {
  position: absolute;
  top: 50%;
  right: 1rem;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  margin-top: -10px;
  width: 20px;
  height: 20px; }
  header .below .search-field button svg {
    display: block;
    width: 20px;
    height: 20px;
    fill: #FFFFFF; }

/* Below */
header .below {
  padding: 0;
  background-color: #FFF;
  position: relative; }
  header .below:after {
    content: '';
    display: table;
    clear: both; }

header .below .right-items {
  text-align: right;
  float: right; }

